@import 'varibles'
@import 'animations'

body
    margin: 0
    padding: 0
    height: 100vh
    background: #29272a

body.modo-claro
    background: rgb(12,129,42)
    background: linear-gradient(90deg, rgba(12,129,42,1) 0%, rgba(105,210,102,1) 50%, rgba(32,146,62,1) 100%)
  
.container-navbar
    position: fixed
    width: 60px
    height: 100vh 
    display: flex
    flex-direction: column
    justify-content: center
    gap: 50px
    transition: width 0.6s ease
    background: #293431
    overflow: hidden
    z-index: 999999

    &:hover
        width: 190px

    .navbar
        position: relative
        color: #fff
        cursor: pointer
        display: flex
        align-items: center
        white-space: nowrap
        left: 20px
        transition: transform 0.6s ease, opacity 0.6s ease


        &:not(:last-child):hover
            transform: scale(1.1) 
            transition: 0.2s

        &:not(:last-child):hover::before
            content: ''
            position: absolute
            left: -10px
            width:88%
            height: 40px
            background: #7a0808
            border-radius: 50px
            
                   
        .fa-solid
            font-size: 1.3em
            transform: translateX(0)
            transition: transform 0.6s ease

        a
            margin-left: 10px
            text-decoration: none
            color: #fff
            font-family: $fontBebas
            font-size: 1.2em
            opacity: 0
            transform: translateX(-20px)
            transition: opacity 0.6s ease, transform 0.6s ease

    &:hover .navbar a
        opacity: 1
        transform: translateX(10%)

    &:hover .navbar .fa-solid
        transform: translateX(0)



.darkmode
    position: relative 
    top: 20%    
    width: 0


    & .btn-container
        position: absolute
        display: flex
        align-items: center
        transform: translate(20px)
        width: 60px
        height: 30px
        border-radius: 20px
        background-color: #ffffff79
        opacity: 0
        transition: opacity 0.6s ease, transform 0.6s ease


        & button
            position: absolute
            left: 5px
            width: 20px
            height: 20px
            border-radius: 50%
            cursor: pointer
            transition: opacity 0.6s ease, transform 0.6s ease
            

.container-navbar:hover .btn-container
    transform: translate(40px)
    opacity: 1
    transition: opacity 0.6s ease, transform 0.6s ease


.carcaca-pc
    display: flex
    justify-content: center
    align-items: center
    position: relative
    left: 20%
    top: 35%
    width: 330px
    height: 190px   
    background-color: #211f1f
    border-radius: $bordaAcimaMac
    

    &::before
        content: ''
        display: flex
        justify-content: center
        align-items: center
        background-color: #fff
        width: 100%
        height: 15%
        left: 0
        position: absolute
        bottom: -30px
        border-radius: $borderAbaixoMac
        border: 1px solid #000

    &::after
        content: ''
        position: absolute
        bottom: -23px  
        height: 15px
        width: 15px
        border-radius: 25px
        background-color: #000000 
        z-index: 999999

    & .tela
        width: 95%
        height: 90%
        background: linear-gradient(to bottom, #9e6f6f, #5f3f3f)

.base
    position: absolute
    bottom: -39px

    &::before
        content: ''
        position: absolute
        clip-path: polygon(7% 0, 94% 0, 100% 100%, 0% 100%)
        top: -10px
        left: 50%
        transform: translateX(-50%)
        width: 60px
        height: 25px
        background-color: #333333c2

    &::after 
        content: ''
        position: absolute
        transform: translate(-50%)
        top: 15px
        width: 90px
        height: 5px
        background-color: #5d5858
        border-radius: 20px
   

.lampada
    position: absolute
    width: 100px
    height: 30px
    display: none
    animation: falhaLuz 2s ease forwards